.footer{
  position: fixed;
  bottom: 0;
  
  width: 100%;
  height: 30px;
  box-shadow: 0 3px 6px $color-primary-shadow;
  //padding-top: 1em;


  &__container{
    width: 100%;
    height: 100%;
    background-color: $color-black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 2em;
    padding-right: 2em;
    &--logo{
      img{
        height: 28px;

      }

      
    }
    &--buttons{
      width: 40%;
      justify-content: space-between;
      a{
        color:$color-white;
        padding-left: 1em;
        padding-right: 1em;
      }
    }
    &--contact{
      display: flex;
      div{
        display:flex;
        padding-right: 1em;
        padding-left: 1em;
        align-items: center;
        color:$color-white;

      }
      @include mobile{
        display: none;
      }
    }




  }

}
#tiktok{
  border-radius: 50%;
}